import React, { useEffect, useState } from 'react'
import Nav from '../Nav/Nav'
import auth from '../../utils/auth';
import Modal from '../Modal/Modal';
import Popup from '../Popup/Popup';


function Tarif() {
  useEffect(() => {
    document.title = "Tarif"
  }, []);
  const [tarif, setTarif] = useState({});
  const [pay, setPay] = useState(null);
  const [statusMessage, setStatusMessage] = useState('');


  const [modalActive, setModalActive] = useState(false);



  useEffect(() => {
    handleGetTarif();
  }, [])

  const handleGetTarif = () => {
    const jwt = localStorage.getItem('jwt');
    if (jwt) {
      auth
        .getTarif(jwt)
        .then((tarif) => {
          setTarif(tarif)
        })
        .catch(e => console.error(e.message));
    }
  }

  const handlePayTarif = () => {
    const jwt = localStorage.getItem('jwt');
    if (jwt) {
      auth
        .payTarif(jwt)
        .then((pay) => {
          setTarif(pay)
          setPay(true)
          setStatusMessage(' ')
          setTimeout(() => {
            setModalActive(false);
            setStatusMessage('')
            setPay(null)
          }, 2000)

        })
        .catch(() => {
          setPay(false)
          setStatusMessage(' ')
        });
    }
  }


  const handleClosePopup = () => {
    setModalActive(false);
    setPay(null)
    setStatusMessage('')
  }
  const handleOpenPopup = () => {
    setModalActive(true);
  }

  return (
    <>
      <main className="main">
        <div className="container">
          <Nav />
          <section className="main__profile profile tarif">
            <div className="profile__body">
              <h1 className="profile__title title"></h1>
              {
                !tarif.isPaid ?
                  (<button className='link link_active' style={{ marginBottom: '15px' }} onClick={handleOpenPopup}> </button>)
                  :
                  (<p className="text text_size_normal tarif__subtitle" style={{ color: 'green' }}>   {tarif.nextPayTime}</p>)
              }


              <p className="text text_size_normal tarif__subtitle">    Infinity Post, Infinity
                Browser, infinity incognito ( ),        .    990    
                 INFY.</p>
              <section className="banner__buttons wallet__buttons">
                <div className="tarif__card">
                  <p className="text text_size_normal tarif__text tarif__text_type_email">    </p> <button
                    className="wallet__button link link_active" href="/tarif" disabled></button>
                </div>
                <div className="tarif__card">
                  <p className="text text_size_normal tarif__text tarif__text_type_browser">   </p> <button
                    className="wallet__button link link_active" href="/tarif" disabled></button>
                </div>
                <div className="tarif__card">
                  <p className="text text_size_normal tarif__text tarif__text_type_messager">   </p> <button
                    className="wallet__button link link_active" href="/tarif" disabled></button>
                </div>
              </section>
            </div>
          </section>
        </div>
      </main>
      <Modal active={modalActive}>
        <Popup onClose={handleClosePopup} >
          <p className="form__title"></p>
          <p className="form__text-subtitle">
             :&nbsp;
            <span className="form__text-subtitle_count">
              {tarif.balance}
            </span>
          </p>
          <p className="form__text-subtitle">
             :&nbsp;
            <span className="form__text-subtitle_count">
              {tarif.tarifCostCoins} INFY (990 &#x20bd;)
            </span>
          </p>
          <p className="form__text-subtitle">
            <b>  ?</b>
          </p>
          <p className="form__text-subtitle" style={pay ? { color: 'green' } : { color: 'red' }}>
            <b>{statusMessage}</b>
          </p>




          <input
            type="button"
            className="link link_active"
            value=""
            onClick={handlePayTarif}
          />

        </Popup>
      </Modal>
    </>
  )
}

export default Tarif
